<template>
<div style="">
      <el-form v-model='getCarForm'>
      <el-row>
        <el-col :span='5'>
           <el-form-item label='车场名称' class='laeblStyle'>
              <el-select v-model='getCarForm.parkingId' class='handle-input' v-show='parkingIdOptionsShow' clearable>
                  <el-option
                  v-for='item in parkingIdOptions'
                  :key='item.id'
                  :label='item.label'
                  :value='item.value'>
                  </el-option>
              </el-select>
           </el-form-item>
        </el-col>
        <el-col :span='5'>
           <el-form-item label='车辆颜色' class='laeblStyle'>
             <el-select v-model='getCarForm.plateColorNo' class='handle-input' v-if='plateColorOptionsShow' clearable>
                <el-option
                v-for='item in plateColorOptions'
                :key='item.id'
                :label='item.label'
                :value='item.value'>
                </el-option>
              </el-select>
           </el-form-item>
        </el-col>
        <el-col :span='5'>
          <el-form-item label='车牌号' class='laeblStyle'>
