            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span='5'>
          <el-form-item label='车辆分类' class='laeblStyle'>
              <el-select v-model='getCarForm.vehicleType' class='handle-input'  v-show='vehicleTypeOptionsShow' clearable>
                  <el-option
                  v-for='item in vehicleTypeOptions'
                  :key='item.id'
                  :label='item.label'
                  :value='item.value'>
                  </el-option>
                </el-select>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span='3'>
          <el-button type="primary" @click="getCarMsg()" >查询</el-button>
        </el-col>
      </el-row>
  </el-form>
    <!-- 第二个表格 -->
 <el-table
      :data="tableCar"
      style="width: 100%;padding:10px 20px">
