      </el-table-column>
      <el-table-column
        prop="ownerPhone"
        label="车主号码">
      </el-table-column>
      <!-- 操作 -->
      <el-table-column
      label="操作" width='260'>
      <template slot-scope="scope">
          <el-button
              @click="handleDetailMsg(scope.$index, scope.row,scope)"
              type="primary"
              size="small">详情
          </el-button>
          <el-button
              @click="handleChangeMsg(scope.$index, scope.row,scope)"
