                <el-input v-model='detailForm.vehicleClassifyName' class='handle-input'></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <!-- 第二行 -->
          <el-row>
            <el-col :span='6'>
                <el-form-item label='车辆品牌' class='laeblStyle'>
                    <el-input v-model='detailForm.vehicleModel' class='handle-input'></el-input>
                  </el-form-item>
            </el-col>
            <el-col :span='6'>
                <el-form-item label='车辆所有人' class='laeblStyle'>
                    <el-input v-model='detailForm.vehicleOwner' style='width:120px;'></el-input>
                  </el-form-item>
            </el-col>
            <el-col :span='6'>
                <!-- <el-form-item label='车辆类型' class='laeblStyle'>
                    <el-input v-model='detailForm.vehicleOwner' class='handle-input'></el-input>
                  </el-form-item> -->
                  <el-col :span='6'>
                      <el-form-item label='身份证' class='laeblStyle'>
                         <el-input v-model='detailForm.ownerIdNo' class='handle-input'></el-input>
                      </el-form-item>
                   </el-col>
            </el-col>
            <el-col :span='6'>
                <el-form-item label='性别' class='laeblStyle'>
                  <el-radio-group v-model='detailForm.ownerGender'>
                    <el-radio :label='1'>男</el-radio>
                    <el-radio :label='2'>女</el-radio>
                 </el-radio-group>
                </el-form-item>
            </el-col>
          </el-row>
          <!--第三行 -->
          <el-row>
            <el-col :span='6'>
                <el-form-item label='生日' class='laeblStyle'>
                    <el-input v-model='detailForm.ownerBirth' class='handle-input'></el-input>
                </el-form-item>
            </el-col>
            <el-col :span='6'>
                <el-form-item label='手机号' class='laeblStyle'>
                    <el-input v-model='detailForm.ownerPhone' class='handle-input'></el-input>
                </el-form-item>
            </el-col>
            <el-col :span='6'>
                <el-form-item label='地址' class='laeblStyle'>
                    <el-input v-model='detailForm.ownerAddress' class='handle-input'></el-input>
                </el-form-item>
            </el-col>
            <!-- <el-col :span='6'>
                <el-form-item label='车辆类型' class='laeblStyle'>
                    <el-input v-model='detailForm.vehicleType' class='handle-input'></el-input>
                </el-form-item>
            </el-col> -->
          </el-row>
          <!-- 添加的行 -->
          <el-row>
          </el-row>

          <!-- 月租车类型 -->
          <div v-show='false'>
            <el-row >
              <el-col :span='6'>
                <el-form-item label='rfidNo' class='laeblStyle'>
                  <el-input v-model='detailForm.rfidNo' class='handle-input'></el-input>
                </el-form-item>
              </el-col>
              <el-col :span='6'>
                  <el-form-item label='月租类型' class='laeblStyle'>
                      <el-input v-model='detailForm.vehicleTypeNo' class='handle-input'></el-input>
                    </el-form-item>
              </el-col>
              <el-col :span='6'>
                  <el-form-item label='自动续月' class='laeblStyle'>
                      <el-input v-model='detailForm.isRelet' class='handle-input'></el-input>
                    </el-form-item>
              </el-col>
              <el-col :span='6'>
                  <el-form-item label='月租金额' class='laeblStyle'>
                      <el-input v-model='detailForm.monthlyRent' class='handle-input'></el-input>
                    </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span='6'>
                <el-form-item label='总租金' class='laeblStyle'>
                  <el-input v-model='detailForm.totalRent' class='handle-input'></el-input>
                </el-form-item>
              </el-col>
            </el-row>
        </div>

        </el-form>
      
    <!-- </div> -->
    </el-dialog>
       <!-- 编辑模态框 -->
       <el-dialog title="详细信息" 
       :visible.sync="dialogVisible2"  width='70%'>
   
       <!-- <div style='height:60vh;position:relative;width:100%;' id='detailBox'> -->
           <el-form v-model='detailForm2' >
             <el-row>
               <el-col :span='6'>
                 <el-form-item label='车场名称' class='laeblStyle'>
                   <el-select v-model='detailForm2.parkingId' class='handle-input'>
                      <el-option
                      v-for='item in parkingIdOptions'
                      :key='item.id'
                      :label='item.label'
                      :value='item.value'>
                      </el-option>
                  </el-select>

                 </el-form-item>
               </el-col>
               <el-col :span='6'>
                 <el-form-item label='车牌号' class='laeblStyle' >
                   <el-input v-model='detailForm2.plateNo' class='handle-input'></el-input>
                 </el-form-item>
               </el-col>
               <el-col :span='6'>
                 <el-form-item label='车牌颜色' class='laeblStyle'>
                    <el-select v-model='detailForm2.plateColor' class='handle-input' v-if='plateColorOptionsShow'>
                        <el-option
                        v-for='item in plateColorOptions'
                        :key='item.id'
                        :label='item.label'
                        :value='item.value'>
                        </el-option>
                      </el-select>
                 </el-form-item>
               </el-col>
               <el-col :span='6'>
                 <el-form-item label='车辆分类' class='laeblStyle'>
                  <!-- 车辆分类 -->
                  <el-select v-model='detailForm2.vehicleType' class='handle-input'  v-show='vehicleTypeOptionsShow' clearable>
                      <el-option
                      v-for='item in vehicleTypeOptions'
                      :key='item.id'
                      :label='item.label'
                      :value='item.value'>
                      </el-option>
                    </el-select>


                 </el-form-item>
               </el-col>
             </el-row>
             <!-- 第二行 -->
             <el-row>
               <el-col :span='6'>
                   <el-form-item label='车辆品牌' class='laeblStyle'>
                       <el-input v-model='detailForm2.vehicleModel' class='handle-input'></el-input>
                     </el-form-item>
               </el-col>
               <el-col :span='6'>
                   <el-form-item label='车辆所有人' class='laeblStyle'>
                       <el-input v-model='detailForm2.vehicleOwner'  style='width:120px'></el-input>
                     </el-form-item>
               </el-col>
               <el-col :span='6'>
                     <el-form-item label='身份证' class='laeblStyle'>
                        <el-input v-model='detailForm2.ownerIdNo' class='handle-input'></el-input>
                     </el-form-item>
               </el-col>
               <el-col :span='6'>
                  <el-form-item label='性别' class='laeblStyle'>
                      <el-radio-group v-model='detailForm2.ownerGender'>
                        <el-radio :label='1'>男</el-radio>
                        <el-radio :label='2'>女</el-radio>
                     </el-radio-group>
                  </el-form-item>
               </el-col>
             </el-row>
             <!--第三行 -->
             <el-row>
               <el-col :span='6'>
                   <el-form-item label='生日' class='laeblStyle'>
                       <el-date-picker
												    v-model="detailForm2.ownerBirth"
												    type="date"
												    placeholder="选择日期" class='handle-input'>
											</el-date-picker>
                   </el-form-item>
               </el-col>
               <el-col :span='6'>
                   <el-form-item label='手机号' class='laeblStyle'>
                       <el-input v-model='detailForm2.ownerPhone' class='handle-input'></el-input>
                   </el-form-item>
               </el-col>
               <el-col :span='6'>
                   <el-form-item label='地址' class='laeblStyle'>
                       <el-input v-model='detailForm2.ownerAddress' class='handle-input'></el-input>
                   </el-form-item>
               </el-col>
               <!-- <el-col :span='6'>
                   <el-form-item label='车辆类型' class='laeblStyle'>
                       <el-select v-model='detailForm2.vehicleType' class='handle-input' >
                          <el-option
                          v-for='item in vehicleTypeOptions'
                          :key='item.id'
                          :label='item.label'
                          :value='item.value'>
                          </el-option>
                      </el-select>
                   </el-form-item>
               </el-col> -->
             </el-row>
             <!-- 月租车类型 -->
             <div v-show='false'>
               <el-row >
                 <el-col :span='6'>
                   <el-form-item label='rfidNo' class='laeblStyle'>
                     <el-input v-model='detailForm2.rfidNo' class='handle-input'></el-input>
                   </el-form-item>
                 </el-col>
                 <el-col :span='6'>
                     <el-form-item label='月租类型' class='laeblStyle'>
                         <el-input v-model='detailForm2.vehicleTypeNo' class='handle-input'></el-input>
                       </el-form-item>
                 </el-col>
                 <el-col :span='6'>
                     <el-form-item label='自动续月' class='laeblStyle'>
                         <el-input v-model='detailForm2.isRelet' class='handle-input'></el-input>
                       </el-form-item>
                 </el-col>
                 <el-col :span='6'>
                     <el-form-item label='月租金额' class='laeblStyle'>
                         <el-input v-model='detailForm2.monthlyRent' class='handle-input'></el-input>
