               </el-row>
               <!-- <el-row> -->
               <!-- </el-row> -->
           </div>
           <el-row>

           </el-row>
           <div style='float:right;margin:20px 15% 0 0;'>
							<el-button type='button' class='el-button el-button--warning' style="width: 80px;margin-right:20px;" @click='cancel()'>取消</el-button>
						<el-button type='button' class='el-button el-button--primary' style="width:80px" @click='saveMsg(detailForm2.vehicleTypeNo)'>保存</el-button>
					</div>
					<div style='clear:both'></div>



           </el-form>
         
       <!-- </div> -->
       </el-dialog>


</div>
</template>
<script>
  import moment from 'moment'
  import qs from 'qs'
  import * as carMsg from '../../api/carMsg'
  export default {
    methods: {
      //删除接口
      handleDelMsg(index,row){
        console.log(index)
        console.log(row)
        var self=this;
        var url=self.baseUrl+"vehicleInfo/deleteVehicle?id="+row.id;
        self.$axios.get(url).then(function(res){
          console.log("del res。。")
          console.log(res.data)
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            self.getCarMsg();
          }else{
            self.$message({
              message:res.data.message,
              type:"info"
            })
          }
        }).catch(err=>console.log(err))
      },
      cancel(){
        this.dialogVisible2=false;
      },
      getCarType(){
      var self=this;
			var url=self.baseUrl+"vehicleInfo/initVehicleClassify"
			carMsg.initVehicleClassify().then(function(res){
				if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.vehicleTypeOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].vehicleClassifyName,
							value:res.data.data[i].id,
						}
					}
				}
			}).catch(function(err){
				console.log(err);
			})
      },
      getCarColor(){
        var self=this;
      var url=self.baseUrl+"vehicleInfo/plateColorList"
      carMsg.plateColorList().then(res=>{
        if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.plateColorOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].plateColor,
							value:res.data.data[i].plateColorNo,
						}
					}
				}
      }).catch(function(err){
				console.log(err);
			})
      },
      getParkingId(){
        var self=this;
        carMsg.parkingId(localStorage.getItem('userId')).then(res=>{
        if(res.data.status=='0'){
					for(var i=0;i<res.data.data.length;i++){
						self.parkingIdOptions[i]={
							id:res.data.data[i].id,
							label:res.data.data[i].parkingName,
							value:res.data.data[i].id
						}
          }
          self.$forceUpdate();
				}
        }).catch(function(err){
				console.log(err);
			})
      },
      saveMsg(val){
        var self=this;
        console.log('this.detailForm2.plateColorNo');
        console.log(self.detailForm2.vehicleType);
          var params={
            "id":self.row.id,
            "parkingId":self.detailForm2.parkingId,
            "vehicleTypeId":String(self.detailForm2.vehicleType),
            "plateNo":self.detailForm2.plateNo,
            "plateColorNo":self.detailForm2.plateColorNo,
            "plateColor": self.detailForm2.plateColor,
            "vehicleModel":self.detailForm2.vehicleModel,

            "vehicleOwner":self.detailForm2.vehicleOwner,
            "ownerIdNo":self.detailForm2.ownerIdNo,
            "ownerGender":self.detailForm2.ownerGender,
            "ownerBirth":self.detailForm2.ownerBirth,
            "ownerPhone":self.detailForm2.ownerPhone,
            "ownerAddress":self.detailForm2.ownerAddress,
            "rfidNo":self.detailForm2.rfidNo,
          }
        self.axiosChangeCar(params);
      },
      axiosChangeCar(params){
        var self=this;
        var url=self.baseUrl+"vehicleInfo/update";
        self.$axios.post(url,params).then(function(res){
          console.log('res.data.......');
          console.log(res.data);
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            self.dialogVisible2=false;
          }else{
            self.$message({
              message:res.data.message,
              type:"info"
            })
          }
        }).catch(function(err){
          console.log(err);
        })              
      },
      currentChange(currentPage){
          this.getTime();
          var self=this;
          this.currentPage=currentPage;
            var url='http://58.210.46.54:8000/listGcjlData';
            var paramString={
              'start':self.momentTime(this.tiemVal[0]),
              'end':self.momentTime(this.tiemVal[1]),
              'hasPayed':self.zfVal,
              'plateNum':this.inputCph,
              'current':this.currentPage,
              'size':this.pageSize
            }
            var jsonParams=JSON.stringify(paramString);
          this.axiosMsg(jsonParams);
      },
      handleSizeChange(val){
          this.pageSize=val;
          var self=this;
          var url='http://58.210.46.54:8000/listGcjlData';
          var str={
              'start':self.momentTime(this.tiemVal[0]),
              'end':self.momentTime(this.tiemVal[1]),
              'hasPayed':self.zfVal,
              'plateNum':this.inputCph,
              'current':this.currentPage,
              'size':val
          }
          var jsonParams=JSON.stringify(str);
           this.axiosMsg(jsonParams);
          console.log(`每页显示${val}条`)
      },
      axiosMsg(params){
        var self=this;
        this.paginationShow=true;
        // this.$axios({
        //   method:"post",
        //   url:url,
        //   headers:{
        //     "Content-Type":"application/json"
        //   },
        //   data:params
        // })
        carMsg.vehicleListQuery(params).then(function(response){
          self.total=response.data.data.total;
          self.tableCar=response.data.data.records;
        }).catch(function(err){
          console.log(err);
        })
      },
      momentTime(val){
          return moment(val).format("YYYY-MM-DD HH:mm:ss");
      },
      momentTime1(val){
          return moment(val).format("YYYY-MM-DD");
      },
      getCarMsg(){
        var self=this;
        console.log(this.getCarForm.vehicleType);
          var url=self.baseUrl +"vehicleInfo/vehicleList"
          //查询，获取参数
          var str={
            'parkingId':self.getCarForm.parkingId,
            'plateColorNo':self.getCarForm.vehicleId,
            "plateNo":self.getCarForm.plateNo,
            "vehicleClassifyId":this.getCarForm.vehicleType,
            "plateColorNo":self.getCarForm.plateColorNo
          }
          var jsonParams=JSON.stringify(str);
          this.axiosMsg(jsonParams);
      },
      handleDetailMsg(index,row,scope){
        var self=this;
        this.dialogVisible=true;
        this.detailForm=row;
        console.log(row)
        this.detailForm.ownerBirth=self.momentTime1(this.detailForm.ownerBirth);
        this.detailForm.vehicleTypeId=(this.detailForm.vehicleType);
        self.detailType=true;

      },
      handleChangeMsg(index,row,scope){
        console.log("车辆编辑。。。")
        var self=this;
        self.dialogVisible2=true;
        var url=self.baseUrl+"vehicleInfo/detail?id="+row.id;
        //调用回显
        self.$axios.get(url).then(function(res){
          console.log("车辆回显数据。。。")
          self.detailForm2=res.data.data
          console.log(self.detailForm2);
        }).catch(err=>console.log(err))
        this.row=row;
        // self.detailForm2.ownerBirth=self.momentTime1(this.detailForm2.ownerBirth);
      }
    },
    data() {
      return {
        getCarForm:{},
          inputCph:'',
          paginationShow:false,
          tableShow:[],
          tableCar:[],
          total:0,
          currentPage:2,
          pageSize:10,
          dialogVisible:false,
          row:[],
          imgRul2:'',
          zfVal:'',
          zfOptions:[
            {id:1,label:"已支付",value:"true"},
            {id:2,label:"未支付",value:"false"}
          ],
          tiemVal:'',
          detailForm:{},
          detailType:false,
          detailType2:false,
          dialogVisible2:false,
          detailForm2:{},
          parkingIdOptionsShow:true,
          parkingIdOptions:[],
          vehicleTypeOptionsShow:true,
          vehicleTypeOptions:[],
          plateColorOptions:[],
          plateColorOptionsShow:true,
      }
    },
    mounted:function(){
        this.getParkingId();
        this.getCarColor();
        this.getCarType();
        this.getCarMsg();
    }
  }
</script>
<style scoped>
.customWidth{
  /* width:10%; */
}
.fl{
  float:left;
